.NbSparkle {
    animation: sparkleColorRotate 6s linear 0s infinite;
}

@keyframes sparkleColorRotate {
    from {
        color: #0000ff;
    }
    10% {
        color: #14a2ff;
    }
    50% {
        color: #1b68f6;
    }
    75% {
        color: #6300e3;
    }
    100% {
        color: #1515fd;
    }
}